import React from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import '../assets/index.scss'
import '../assets/css/bootstrap.min.css';
import '../assets/css/plugins/animate.css';
import '../assets/css/style.css';
import '../assets/css/themes/blue-orange.css';
import '../assets/css/plugins/animate.css';
import '../assets/vendor/themify/themify.css';

import bootstrap from 'script-loader!../assets/js/lib/jquery-2.2.0.min.js';
import jquery from 'script-loader!../assets/js/lib/bootstrap.min.js';
import main from 'script-loader!../assets/js/main.js';
import animated_headline from 'script-loader!../assets/js/animated-headline.js';

import HeaderComponent from './header';

import logo from '../assets/images/logo.png';

const TemplateWrapper = ({ children }) => (
  <div>
    <Helmet
      title="DuBase"
      meta={[
        { name: 'description', content: 'Sample' },
        { name: 'keywords', content: 'sample, something' },
      ]}
    />
    <HeaderComponent />
    {children()}
  </div>
)

TemplateWrapper.propTypes = {
  children: PropTypes.func,
}

export default TemplateWrapper


