import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import logo from '../assets/images/logo.png';
class HeaderComponent extends Component {
    render() {
        return (
            <nav className="navbar navbar-default navbar-fixed-top" style={{background:'#fff'}}>
                <div className="container">

                    <div className="navbar-header">
                        <Link className="navbar-brand" to="#header">
                            <img src={logo} alt="" />
                        </Link>
                        <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#main-navbar" aria-expanded="false">
                            <span className="sr-only">Toggle navigation</span>
                            <span className="icon-bar"></span>
                            <span className="icon-bar"></span>
                            <span className="icon-bar"></span>
                        </button>
                    </div>
                    <div className="collapse navbar-collapse" id="main-navbar">
                        <ul className="nav navbar-nav navbar-right">
                            <li>
                                <Link to="#header">Home</Link>
                            </li>
                            <li>
                                <Link to="#process">Process</Link>
                            </li>
                            <li>
                                <Link to="#features">Features</Link>
                            </li>
                            <li>
                                <Link to="#contact">contact</Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        );
    }
}

export default HeaderComponent;


